import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-Unidadperiodo3a-create',
  templateUrl: 'unidadperiodo3a-create.html',
})
export class Unidadperiodo3aCreatePage {

  public form                  : FormGroup;
  username: string = "";
  public idtipo: string="";
  public idtipo2: string="";
  public idnombre: string="";

  public ano                 : any;
  public nombre                 : any;
  public estado                 : any;
  public codigo                 : any;

  idempresa:any;
  private _ID                  : String;
  public pageTitle           	: string;

  constructor( public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({
       nombre     : ['', Validators.required],
       estado     : ['', Validators.required],
       ano     : ['', Validators.required],
       codigo     : ['', Validators.required]


     });


  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  ionViewDidLoad() : void
  {
    this.idtipo=this.navParams.data.record2.idtipo;
    this.idtipo2=this.navParams.data.record2.idtipo2;
    this.idnombre=this.navParams.data.record2.idnombre;
    if(this.navParams.get("record")==null)
    {


      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.estado		=	this.navParams.data.record.estado;
       this.codigo		=	this.navParams.data.record.codigo;
       this.ano		=	this.navParams.data.record.ano;

       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }



  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
     let   options       : any	     = {
      idtipounidad : this.idtipo,
      idunidadacademica : this.idtipo,
     nombre : this.form.controls['nombre'].value,
     estado : this.form.controls['estado'].value,
     codigo : this.form.controls['codigo'].value,
     ano : this.form.controls['ano'].value,

     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' aÃ±o periodo de calusac '}
    };


     if(this.navParams.get("record")==null)
     {
          if(options){
            this.authService.createReg('',options,'unidadperiodo3as').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'unidadperiodo3as').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


}



